<template>
    <div id="current">
        <div class="field-part">
            <mt-cell title=""
                     v-model=" dataForm.tranAccount "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>
        </div>
    
        <div class="field-part">
            <mt-cell title=""
                     v-model=" dataForm.tranAccountChild "
                     @click.native=" showChildAccountSheet = true "
                     is-link></mt-cell>
            <mt-cell title=""
                     v-model=" dataForm.timeLength ">
                <span v-for="item in timeTags"
                      :class="{'active': activeTags === item.type}"
                      :key="item.type"
                      class="btn"
                      @click="timeChange(item.type)">{{item.name}}</span>
            </mt-cell>
            <mt-cell title=""
                     @click.native="open('startTimePicker')"
                     :value=" dataForm.startDate "></mt-cell>
            <mt-cell title=""
                     @click.native="open('endTimePicker')"
                     :value=" dataForm.endDate "></mt-cell>
        </div>
    
        <div class="field-action">
            <mt-button type="primary"
                       size="large"
                       @click="query()"></mt-button>
        </div>
        <!--        -->
        <mt-datetime-picker ref="startTimePicker"
                            type="date"
                            year-format="{value} "
                            month-format="{value} "
                            date-format="{value} "
                            v-model="startTimevalue"
                            @confirm="getTime('startTimevalue', 'startDate')">
        </mt-datetime-picker>
        <!--        -->
        <mt-datetime-picker ref="endTimePicker"
                            type="date"
                            year-format="{value} "
                            month-format="{value} "
                            date-format="{value} "
                            v-model="endTimeValue"
                            @confirm="getTime('endTimeValue', 'endDate')">
        </mt-datetime-picker>
        <!--        -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>
        <!--        -->
        <mt-actionsheet :actions="accountChildListSheet"
                        v-model="showChildAccountSheet"></mt-actionsheet>
    
    </div>
</template>
<script>

import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';
//
import * as serviceAccount from '../../services/account';

export default {
    name: 'current',
    data() {
        return {
            dataForm: {
                tranAccountChild: '',
                tranAccountChildId: '',
                timeLength: '',
                startDate: '',
                endDate: '',
                tranAccountId: '',
                tranAccount: ''
            },
            timeTags: [{
                type: "week",
                name: ""
            }, {
                type: "month",
                name: ""
            }, {
                type: "year",
                name: ""
            }],
            activeTags: "week",
            accountList: [],
            accountListSheet: [],
            accountChildListSheet: [],
            showAccountSheet: false,
            showChildAccountSheet: false,
            startTimevalue: null,
            endTimeValue: null
        }
    },
    computed: {
        ...mapGetters(['bankCradSelector'])
    },
    methods: {
        open(picker) {  //
            this.$refs[picker].open();
        },
        getTime(value, type) {   //
            let _copy = this.dataForm[type];
            this.dataForm[type] = this[value].format('yyyyMMdd');
            if (this.compareTime()) {
                this.dataForm[type] = _copy;
            }

        },
        compareTime() {     //
            if (parseInt(this.dataForm.endDate) < parseInt(this.dataForm.startDate)) {
                Toast({
                    message: '',
                    position: 'middle',
                    duration: 2000
                })
                return true;
            }
            else {
                return false
            }
        },
        query() {
            this.$router.push('current-list');
        },
        timeChange(type) {
            this.activeTags = type;
            let date = new Date();
            this.dataForm.startDate = date.timeLen(type, 'yyyyMMdd');
            this.dataForm.endDate = date.format('yyyyMMdd');
        },
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
        },
        accountChildCallback(item) {
            this.dataForm.tranAccountChild = item.name;
            this.dataForm.tranAccountChildId = item.id;
        },

    },
    created() {

        this.timeChange("week");

        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            this.accountList = res.data.list;

            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccount) {
                this.dataForm.tranAccount = _firstAccount.account;
                this.dataForm.tranAccountId = _firstAccount.id;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
        serviceAccount.getAccountChildList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccountChild) {
                this.dataForm.tranAccountChild = _firstAccount.account;
                this.dataForm.tranAccountChildId = _firstAccount.id;
            }

            let _copy = [];
            res.data.list.forEach((item, index) => {
                _copy.push({
                    id: item.id,
                    name: item.account,
                    method: this.accountChildCallback,
                })
            });

            this.accountChildListSheet = _copy;
        }, (err) => {
            Toast(err);
        });
        this.$store.dispatch('showLoading', false);

    }

}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
.btn {
    padding: 12px;
    margin: auto 8px;
    color: #2196F3;
    border: 1px solid #2196F3;
    border-radius: 3px;
}

.active {
    color: #fff;
    background-color: #2196F3;
}
</style>


