<template>
    <div>
        <mt-index-list class="mint-indexlist-payee" >
            <mt-index-section :index=" index " v-for="(section, index) in payeeListSection" :key=" index " >
                <li :title=" item.name " v-for="(item, i) in section"  :key=" i " class="mint-indexsection-item" >
                    <a href="javascript:;" @click="selector( item )" >
                        <i class="iconfont icon-cmbchina-logo"></i>
                        <cite class="name">{{ item.name }} </cite>
                        <span class="attr">{{ item.bankName }}  {{ item.account.substr(-4) }}</span>
                        <i class="mint-cell-allow-right"></i>
                    </a>
                </li>
            </mt-index-section>
        </mt-index-list>
    </div>
</template>


<script>

    import { Toast, IndexList, IndexSection } from 'mint-ui-rem-dpr';
    
    import * as servicePayee from '../../services/payee';

    export default {
        data () {
            return {
                payeeList: [],
                payeeListSection: {}
            }
        },
        created() {
            servicePayee.getPayeeList().then((res)=>{
                if( !res.data.status ){
                    Toast(res.data.message);    
                    return;
                }
                let _temp = res.data.list;
                _temp = _temp.sort((a, b)=>{
                    let _x = a.cnName.substr(0,1);
                    let _y = b.cnName.substr(0,1);
                    let _t = ( _x === _y ) ? 0 : (  _x < _y ? -1 : 1 );
                    return _t;
                });
                this.payeeList = _temp;
                
                let _listSection = {};
                _temp.forEach((item, index)=>{
                    let _x = item.cnName.substr(0,1).toUpperCase();
                    if( !(_x in _listSection) ){
                        _listSection[ _x ] = [];   
                    }
                    _listSection[ _x ].push( item );
                });
                this.payeeListSection = _listSection;

                this.$store.dispatch('showLoading', false);

            }, (err)=>{
                Toast(err);
            });
        },
        methods: {
            selector ( item ){
                this.$store.dispatch('changePayee', item);
                this.$router.go( -1 );
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


