import * as types from '../mutation-types';

const state = {
    isShowTabbar: true,
    tabnavId: "0",
    tabnavList: [
        {
            id: '0',
            name: '',
            icon: 'icon-tabbar-home',
            path: '/home'
        },
        {
            id: '1',
            name: '',
            icon: 'icon-tabbar-financial',
            path: '/financial'
        },
        {
            id: '2',
            name: '',
            icon: 'icon-tabbar-life',
            path: '/life'
        },
        {
            id: '3',
            name: '',
            icon: 'icon-tabbar-my',
            path: '/my'
        }
    ]
};

// getters
const getters = {
    // tabnavId: state => state.tabnavId,
    // tabnavList: state => state.tabnavList
};

// actions
const actions = {
    changeTabBar: ({ commit }, data ) => {
        commit(types.CHANGE_TAB_BAR, data);
    }
};

// mutations
const mutations = {
    [types.SHOW_TAB_BAR] (state, data) 
    {
        state.isShowTabbar = data;
    },

    [types.CHANGE_TAB_BAR](state, { id, name }) {
        state.tabnavId = id;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};



