<template>
    <div>
        <div class="field-part">

            <mt-cell title="" class="mint-select" >
                <select name="" id="city" class="mint-field-core" v-model="dataForm.city"  @change="onCityChange">
                    <option :value="item.code"  v-for="item in cityList">
                        {{ item.name }}
                    </option>

                </select>
                <i class="mint-cell-allow-right"></i>
            </mt-cell>
            <mt-cell title=""  class="mint-select" >
                <select name="" id="company" class="mint-field-core" v-model="dataForm.company">
                    <option :value="item.code"  v-for="item in companyList">
                        {{ item.name }}
                    </option>
                </select>
                <i class="mint-cell-allow-right"></i>

            </mt-cell>

            <mt-field label="" placeholder="" v-model=" dataForm.number " disableClear>

            </mt-field>

        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
            <mt-button type="primary"  @click="arrearsQuery()"></mt-button>
        </div>

    </div>
</template>

<script>

    import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

    import * as serviceLife from '../../services/life';
    let lsPage = [ '/life-pay/electricity-expense-history','/life-pay/electricity-expense-confirm'];
    export default {
        data (){
            return {
                lsDataForm: {},
                dataForm: {
                    city: '',
                    cityName: '',
                    company: '',
                    companyName: '',
                    number: ''
                },
                cityList: [],
                companyList: []

            }
        },
        created() {
            serviceLife.getElectricityCityList().then((res)=>{
                if( !res.data.status ){
                    Toast(res.data.message);
                    return;
                }
                this.cityList = res.data.list;
                this.dataForm= Object.assign({}, this.lsDataForm);
                this.$store.dispatch('showLoading', false);

            }, (err)=>{
                Toast(err);
            });
            serviceLife.getElectricityCompanyList().then((res)=>{
                if( !res.data.status ){
                    Toast(res.data.message);
                    return;
                }
                this.companyList = res.data.list;
                this.dataForm=Object.assign({}, this.lsDataForm);
            }, (err)=>{
                Toast(err);
            });
        },
        methods: {
            arrearsQuery (){

                let _data = Object.assign({}, this.dataForm );
                var _error = [];

                if( this.$validate.isEmpty(  _data.city )  )
                {
                    _error.push( '' )
                }
                if( this.$validate.isEmpty(  _data.company ) )
                {
                    _error.push( '' )
                }
                if( this.$validate.isEmpty(  _data.number ) )
                {
                    _error.push( '' )
                }
                if( _error.length > 0 )
                {
                    Toast({
                        message: _error[0] ,
                        iconClass: 'icon-toast-error',
                        duration: 1000
                    });
                    return;
                }

                this.$router.push( 'electricity-expense-confirm' );
            },
            onCityChange (event) {
                let _val = event.target.value || '';

                if( _val.length === 0)
                {
                    return;
                }
                this.dataForm.cityName = event.target.options[event.target.selectedIndex].text;
            }
        },
        beforeRouteEnter (to, from, next) {
            next((vm)=>{
                if(lsPage.indexOf( from.path ) > -1 ){
                    let _temp = vm.$ls.getItem('ahrcu-electricity-expense');
                    vm.lsDataForm = Object.assign({}, vm.dataForm, _temp);
                }
            })
        },
        beforeRouteLeave (to, from, next) {
            if(lsPage.indexOf( to.path ) > -1 ){
                this.$ls.setItem('ahrcu-electricity-expense', this.dataForm );
            }
            next( true );
        },

    }

</script>


<style lang="scss" scoped>

    @import '../../assets/styles/mixins';


</style>



