<template>
    <div class="success">
        <i class="iconfont icon-toast-success "></i>
        <cite></cite>

        <mt-button type="default" size="large" @click.native="back()"></mt-button>
    </div>
</template>
<script>

    import { Toast, Indicator } from 'mint-ui-rem-dpr';

    import { mapGetters, mapActions } from 'vuex';

    export default {
        data (){
            return {
                dataForm: {},
            }
        },
        created(){
            this.$ls.removeItem('ahrcu-bank-card');
            this.$store.dispatch('clearPayee', null);
            this.$store.dispatch('clearBank', null);
        },
        methods: {
            back (){
                this.$router.push( '/home' );
            }
        }
    }
</script>

<style lang="scss" scoped>

    @import '../../assets/styles/mixins';

    .success
    {
        padding: 60px 30px;
        text-align: center;

        .iconfont
        {
            font-size: 48px;
            color: #5CB85C;
        }
        cite
        {
            display: block;
            font-size: 16px;
            line-height: 2em;
            color: #5CB85C;
        }

        .mint-button
        {
            margin-top: 50px;
        }
    }


</style>



