<template>
    <mt-tabbar fixed v-model="tabnavId">
        <mt-tab-item v-for=" item in tabnavList "  @click.native="changeTabBar( item )" :id=" item.id " :key=" item.id " >
            <i slot="icon" class="iconfont" :class=" item.icon "></i>
            {{ item.name }}
        </mt-tab-item>
    </mt-tabbar>
</template>

<script>

import { mapGetters, mapActions } from 'vuex';

export default {
    computed: mapGetters({
        tabnavList: 'tabnavList',
        tabnavId: 'tabnavId'
    }),
    components: {
    },
    methods: {
        changeTabBar: function( data ) {
            this.$router.push( data.path );
            this.$store.dispatch('changeTabBar', data);
        }
    }
};
</script>



<style lang="scss" scoped>
    
    @import "../assets/styles/vendor/compass-mixins/lib/_compass";
    @import "../assets/styles/vendor/compass-mixins/lib/_animate";

    .mint-tabbar
    {
        .mint-tab-item
        {
            @include ellipsis();
            
            @include clearfix();

            &.is-selected
            {
                color: #26a2ff;
            }


            .mint-tab-item-icon
            {
                i
                {
                    display: inline-block;
                }
                
            }
        }
    }
</style>



