<template>
    <div>
        <div class="field-part">
            <mt-cell title="" :value=" dataForm.number "></mt-cell>
            <mt-cell title="" :value=" dataForm.name  "></mt-cell>
            <mt-cell title="" :value=" dataForm.amount  "></mt-cell>
        </div>

        <div class="field-part">
            <mt-field label="" placeholder="" type="number" v-model=" dataForm.payAmount "></mt-field>
            <mt-cell title="" v-model=" dataForm.tranAccount " @click.native=" showAccountSheet = true " is-link></mt-cell>

            <mt-field label="" placeholder="" type="password" v-model=" dataForm.payPwd "></mt-field>
            <mt-field label="" placeholder="" type="number" max-length="6" v-model=" dataForm.payCode "></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
            <mt-button type="primary" size="large" @click.native="submit()"></mt-button>
        </div>
        <mt-actionsheet :actions="accountListSheet" v-model="showAccountSheet"></mt-actionsheet>
    </div>
</template>

<script>

    import { Toast, Actionsheet ,Indicator} from 'mint-ui-rem-dpr';

    import { mapGetters, mapActions } from 'vuex';
    import * as serviceAccount from '../../services/account';
    import * as serviceLife from '../../services/life';
    export default {
        data (){
            return {
                dataForm: {
                    payAmount: '',
                    name: '',
                    tranAccountId: '',
                    tranAccount: ''
                },
                accountList: [],
                accountListSheet: [],
                showAccountSheet: false,
            }
        },
        created(){
            let _temp = this.$ls.getItem('ahrcu-electricity-expense');
            this.dataForm = Object.assign( this.dataForm, _temp );
            this.dataForm.name='';
            this.dataForm.amount=133;
            serviceAccount.getAccountList().then((res)=>{
                if( !res.data.status ){
                    Toast(res.data.message);
                    return;
                }
                this.accountList = res.data.list;

                let _firstAccount = res.data.list[0];

                if( !this.dataForm.tranAccount )
                {
                    this.dataForm.tranAccount = _firstAccount.account;
                    this.dataForm.tranAccountId = _firstAccount.id;
                }

                let _temp = [];

                res.data.list.forEach((item, index)=>{
                    _temp.push( {
                        id: item.id,
                        name: item.account,
                        method: this.sheetCallback,
                    })
                });
                this.accountListSheet = _temp;
            }, (err)=>{
                Toast(err);
            });
        },
        methods: {
            submit (){
                let _data = Object.assign({}, this.dataForm);
                var _error = [];

                if( this.$validate.isEmpty(  _data.payPwd ) )
                {
                    _error.push( '' )
                }
                if( this.$validate.isEmpty(  _data.payCode ) )
                {
                    _error.push( '' )
                }

                if( _error.length > 0 )
                {
                    Toast({
                        message: _error[0] ,
                        iconClass: 'icon-toast-error',
                        duration: 1000
                    });
                    return;
                }

                Indicator.open({
                    text: '...',
                    spinnerType: 'fading-circle'
                });

                setTimeout(()=>{
                    Indicator.close();
                    this.$router.push( 'success' );
                }, 3000);

            }
        }
    }

</script>


<style lang="scss" scoped>

    @import '../../assets/styles/mixins';

    .icon-navs
    {
        padding: 20px 0px;
        @include clearfix();

        .item
        {
            float: left;
            width: 25%;
            a
            {
                display: block;
                padding: 0.4em 0px 0.4em 0px;
                text-align: center;
                min-height: 50px;

                &:active
                {
                    background-color: #f2f2f2;
                }

                .iconfont
                {
                    display: block;
                    height: 60px;
                    line-height: 60px;
                    font-size: 36px;
                    color: #93C23C;
                }

                cite
                {
                    display: block;
                    color: #333;
                    font-size: 12px;
                }
            }
        }
    }

</style>



