import axios from 'axios';

import api from './api';

export function getAccountList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getAccountList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}

export function getAccountChildList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getAccountChildList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}



