import axios from 'axios';

import api from './api';

//
export function getElectricityCityList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getElectricityCityList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}

