import * as types from '../mutation-types';

const state = {
    payeeName: '',
    payeeAccount: '',
    payeeBankName: '',
    payeeBankPhone: '',
    payeeLastUpdate: '',
};

// getters
const getters = {
    payeeName: state => state.payeeName,
    payeeAccount: state => state.payeeAccount,
    payeeBankName: state => state.payeeBankName,
    payeeBankPhone: state => state.payeeBankPhone,
    payeeLastUpdate: state => state.payeeLastUpdate,
};

// actions
const actions = {
    changePayee: ({ commit }, data ) => {
        commit(types.CHANGE_PAYEE, data);
    },
    clearPayee: ({ commit }, data ) => {
        commit(types.CLEAR_PAYEE, data);
    }
};

// mutations
const mutations = {
    [types.CHANGE_PAYEE] (state, data) 
    {
        state.payeeName = data.name;
        state.payeeAccount = data.account;
        state.payeeBankName = data.bankName;
        state.payeeBankPhone = data.phone;
        state.payeeLastUpdate = +new Date();
    },
    
    [types.CLEAR_PAYEE] (state, data) 
    {
        state.payeeName = '';
        state.payeeAccount = '';
        state.payeeBankName = '';
        state.payeeBankPhone = '';
        state.payeeLastUpdate = +new Date();
    },
};

export default {
    state,
    getters,
    actions,
    mutations
};



