<template>
    <div>
        <div class="field-part">
            <mt-field label="" placeholder="" v-model=" bankCradSelector.payeeName " disableClear>
                <router-link to="/selector/payee" class="btn-selector btn-seletor-payee">
                    <i class="iconfont icon-transfer-payee-name"></i>
                </router-link>
            </mt-field>
            <mt-field label="" placeholder="" type="number" v-model=" bankCradSelector.payeeAccount " disableClear></mt-field>
            <mt-cell title="" :value=" bankCradSelector.payeeBankName || '' " href="#/selector/bank" is-link></mt-cell>
        </div>
           
        <div class="field-part">
            <mt-cell title="" v-model=" dataForm.tranAccount " @click.native=" showAccountSheet = true " is-link></mt-cell>
            <mt-cell title="" value="3,567,71" ></mt-cell>
            <mt-field label="" placeholder="" type="number" v-model=" dataForm.tranAmount "></mt-field>
        </div>
        
        <div class="field-part">
            <mt-field label="" placeholder="" type="number" v-model=" dataForm.phone "></mt-field>
            <mt-field label="" placeholder="" type="text" v-model=" dataForm.remark "></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
            <mt-button type="primary" size="large" @click="next()"></mt-button>
        </div>

        <mt-actionsheet :actions="accountListSheet" v-model="showAccountSheet"></mt-actionsheet>

    </div>
</template>
<script>

    import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

    import { mapGetters, mapActions } from 'vuex';

    import * as serviceAccount from '../../services/account';

    let lsPage = [ '/selector/payee', '/selector/bank', '/transfer/bank-card-confirm'];

    export default {
        data (){
            return {
                dataForm: {
                    tranAmount: '',
                    phone: '',
                    remark: '',
                    tranAccountId: '',
                    tranAccount: ''
                },
                accountList: [],
                accountListSheet: [],
                showAccountSheet: false,
            }
        },
        created() {

            this.$store.dispatch('showLoading', false);

            serviceAccount.getAccountList().then((res)=>{
                if( !res.data.status ){
                    Toast(res.data.message);    
                    return;
                }
                this.accountList = res.data.list;

                let _firstAccount = res.data.list[0];

                if( !this.dataForm.tranAccount )
                {
                    this.dataForm.tranAccount = _firstAccount.account;
                    this.dataForm.tranAccountId = _firstAccount.id;
                }

                let _temp = [];

                res.data.list.forEach((item, index)=>{
                    _temp.push( {
                        id: item.id,
                        name: item.account,
                        method: this.sheetCallback,
                    })
                });

                this.accountListSheet = _temp;
            }, (err)=>{
                Toast(err);
            });
        },
        computed: {
            ...mapGetters(['bankCradSelector']),
        },
        methods: {
            next(){

                let _data = Object.assign({}, this.dataForm, this.bankCradSelector );
                var _error = [];

                if( this.$validate.isEmpty(  _data.payeeName ) )
                {
                    _error.push( '' )
                }

                if( this.$validate.isEmpty(  _data.payeeAccount ) )
                {
                    _error.push( '' )
                }
                
                if( this.$validate.isEmpty(  _data.payeeBankName ) )
                {
                    _error.push( '' )
                }
                
                if( this.$validate.isEmpty(  _data.tranAmount ) )
                {
                    _error.push( '' )
                }
                if( !this.$validate.isAmount(  _data.tranAmount ) )
                {
                    _error.push( '' )
                }
                
                if( !this.$validate.isEmpty(  _data.phone ) && !this.$validate.isPhone(  _data.phone ) )
                {
                    _error.push( '' )
                }

                if( _error.length > 0 )
                {
                    Toast({
                        message: _error[0] ,
                        iconClass: 'icon-toast-error',
                        duration: 1000
                    });
                    return;
                }

                this.$router.push( 'bank-card-confirm' );
                                    
            },
            sheetCallback( item ){
                this.dataForm.tranAccount = item.name;
                this.dataForm.tranAccountId = item.id;
            }
        },
        beforeRouteEnter (to, from, next) {
            next((vm)=>{
                if(lsPage.indexOf( from.path ) > -1 ){
                    let _temp = vm.$ls.getItem('ahrcu-bank-card');
                    vm.dataForm = Object.assign( vm.dataForm, _temp );
                }
            })
        },
        beforeRouteLeave (to, from, next) {
            if(lsPage.indexOf( to.path ) > -1 ){
                this.$ls.setItem('ahrcu-bank-card', this.dataForm );
                this.$store.dispatch('changePayee', {
                    name: this.bankCradSelector.payeeName,
                    account: this.bankCradSelector.payeeAccount,
                    bankName: this.bankCradSelector.payeeBankName,
                });
            }
            else{
                this.$store.dispatch('clearPayee', null);
                this.$store.dispatch('clearBank', null);
            }
            next( true );
        },
    }
</script>

<style lang="scss" scoped>
    
    @import '../../assets/styles/mixins';


</style>


