<template>
    <div class=" popup-picker " :class="{ show : show }">
        {{ show }}
    </div>
</template>

<script>
    export default {
        props: [ 'show' ],
        data () {
            return {
                
            }
        }
    }
</script>

<style lang="scss" scoped>
    
    @import '../assets/styles/mixins';
    

    .popup-picker
    {
        position: fixed;
        top: 100%;
        left: 0px;
        z-index: 999;
        width: 100%;
        height: 100%;
        background-color: rgba(#000, 0.6);

        @include transition(top, 0.2s);

        &.show
        {
            top: 0px;
        }
    }
</style>



