import Vue from 'vue';
import store from './../store';

import VueRouter from 'vue-router';
Vue.use(VueRouter);

import home from '../pages/home';

import myAccount from '../pages/myAccount/index';
import current from '../pages/myAccount/current';
import accountEdit from '../pages/myAccount/account-name-edit';
import accountDoEdit from '../pages/myAccount/account-name-doEdit';
import accountEditResult from '../pages/myAccount/account-name-editResult';
import currentList from '../pages/myAccount/current-list';
import currentListDetail from '../pages/myAccount/currentList-detail';

import transfer from '../pages/transfer/index';
import bankCard from '../pages/transfer/bank-card';
import transferSuccess from '../pages/transfer/success';
import bankCardConfirm from '../pages/transfer/bank-card-confirm';
import bankPhone from '../pages/transfer/bank-phone';

import selectorPayee from '../pages/selector/payee';
import selectorBank from '../pages/selector/bank';

import lifePay from '../pages/life-pay/index';
import phoneExpense from '../pages/life-pay/phone-expense';
import phoneExpenseConfirm from '../pages/life-pay/phone-expense-confirm';
import electricityExpense from '../pages/life-pay/electricity-expense';
import electricityExpenseHistory from '../pages/life-pay/electricity-expense-history';
import electricityExpenseHistoryDetail from '../pages/life-pay/electricity-expense-history-detail';
import electricityExpenseConfirm from '../pages/life-pay/electricity-expense-confirm';
import electricitySuccess from '../pages/life-pay/success';
import gasExpense from '../pages/life-pay/gas-expense';
import gasExpenseConfirm from '../pages/life-pay/gas-expense-confirm';
import heatExpense from '../pages/life-pay/heat-expense';
import heatExpenseConfirm from '../pages/life-pay/heat-expense-confirm';
import tvExpense from '../pages/life-pay/tv-expense';
import tvExpenseConfirm from '../pages/life-pay/tv-expense-confirm';
import waterExpense from '../pages/life-pay/water-expense';
import waterExpenseConfirm from '../pages/life-pay/water-expense-confirm';

import creditCard from '../pages/credit-card/index';

const routes = [
    {
        path: '/', component: home, 
        meta: {
            title: '', showHd: true, showTabbar: true, showLoading: true, autoCloseLoading: false
        }
    },
    {
        path: '/home', component: home, 
        meta: {
            title: ''
        }
    },
    {
        path: '/my-account',
        component: myAccount,
        meta: {
            title: '', showTabbar: false, autoCloseLoading: true
        }
    },
    {
        path: '/my-account/current',
        name: 'current',
        component: current,
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/my-account/current-list',
        name: 'current-list',
        component: currentList,
        meta: {
            title: '', showTabbar: false
        },
        scrollBehavior(to, from, savedPosition) {
            if (savedPosition) {
                return savedPosition
            } else {
                return { x: 0, y: 0 }
            }
        }
    },
    {
        path: '/my-account/current-list/detail',
        name: 'account-transfer-detail',
        component: currentListDetail,
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/my-account/edit',
        name: 'account-edit',
        component: accountEdit,
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/my-account/do-edit',
        name: 'account-do-edit',
        component: accountDoEdit,
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/my-account/edit-result',
        name: 'account-edit-result',
        component: accountEditResult,
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/transfer', component: transfer, 
        meta: {
            title: '', showTabbar: false, autoCloseLoading: true
        }
    },
    {
        path: '/transfer/bank-card', component: bankCard, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/transfer/bank-card-confirm', component: bankCardConfirm, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/transfer/success', component: transferSuccess, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/transfer/bank-phone', component: bankPhone, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/selector/payee', component: selectorPayee, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/selector/bank', component: selectorBank, 
        meta: {
            title: '', showTabbar: false
        }
    },

    {
        path: '/life-pay', component: lifePay, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/life-pay/phone-expense', component: phoneExpense, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/life-pay/phone-expense-confirm', component: phoneExpenseConfirm, 
        meta: {
            title: '', showTabbar: false
        }
    },

    {
        path: '/credit-card', component: creditCard, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/life-pay/electricity-expense', component: electricityExpense, 
        meta: {
            title: '', showTabbar: false
        }
    },
    {
        path: '/life-pay/electricity-expense-history', component: electricityExpenseHistory, 
        meta: {
            title: '-', showTabbar: false
        }
    },
    {
        path: '/life-pay/electricity-expense-confirm', component: electricityExpenseConfirm, 
        meta: {
            title: '-', showTabbar: false
        }
    },
    { path: '/transfer', component: transfer, 
        meta: {
            title: '', showTabbar: false, autoCloseLoading: true
        } 
    },
    { path: '/transfer/bank-card', component: bankCard, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/transfer/bank-card-confirm', component: bankCardConfirm, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/transfer/success', component: transferSuccess, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/transfer/bank-phone', component: bankPhone, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/selector/payee', component: selectorPayee, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/selector/bank', component: selectorBank, 
        meta: {
            title: '', showTabbar: false
        } 
    },

    { path: '/life-pay', component: lifePay, 
        meta: {
            title: '', showTabbar: false
        }
    },
    { path: '/life-pay/phone-expense', component: phoneExpense, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/phone-expense-confirm', component: phoneExpenseConfirm, 
        meta: {
            title: '', showTabbar: false
        } 
    },

    { path: '/credit-card', component: creditCard, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/electricity-expense', component: electricityExpense, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/electricity-expense-history', component: electricityExpenseHistory, 
        meta: {
            title: '-', showTabbar: false
        } 
    },
    { path: '/life-pay/electricity-expense-history-detail', component: electricityExpenseHistoryDetail, 
        meta: {
            title: '-', showTabbar: false
        } 
    },
    { path: '/life-pay/electricity-expense-confirm', component: electricityExpenseConfirm, 
        meta: {
            title: '-', showTabbar: false
        } 
    },
    { path: '/life-pay/success', component: electricitySuccess, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/gas-expense', component: gasExpense, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/gas-expense-confirm', component: gasExpenseConfirm, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/heat-expense', component: heatExpense, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/heat-expense-confirm', component: heatExpenseConfirm, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/tv-expense', component: tvExpense, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/tv-expense-confirm', component: tvExpenseConfirm, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/water-expense', component: waterExpense, 
        meta: {
            title: '', showTabbar: false
        } 
    },
    { path: '/life-pay/water-expense-confirm', component: waterExpenseConfirm, 
        meta: {
            title: '', showTabbar: false
        } 
    }

]

const router = new VueRouter({
    routes
});

// 

router.beforeEach((to, from, next) => {
    //console.log( 'beforeEach' );

    let _showBack = true;
    if (to.path === '/' || to.path === '/home') {
        _showBack = false;
    }
    store.dispatch('showBack', _showBack);

    let _menuName = routes.filter((item) => { return item.path === to.path });
    let meta = null;
    if (_menuName.length > 0) {
        meta = _menuName[0].meta;
        store.dispatch('updateHdTitle', meta.title);
        (store.state.hd.isShowHd !== meta.showHd) && store.dispatch('showHd', meta.showHd !== false);
        (store.state.tabbar.isShowTabbar !== meta.showTabbar) && store.dispatch('showTabbar', meta.showTabbar !== false);
        (meta.showLoading !== false) && store.dispatch('showLoading', true);
    }

    next();

    (meta.autoCloseLoading === true) && store.dispatch('showLoading', false);

    //console.log( 'beforeEach next' )

});

router.afterEach((to, from) => {
    //console.log( 'afterEach' );
});


//   back 
Vue.prototype.$back = (() => {
    router.go(-1)
});

export default router;



