<template>
    <div class="account-name-edit">
        <div class="title">:</div>
        <div class="page-part-top">
            <mt-cell v-for="item in accountList"
                     :key="item.id"
                     @click.native="next(item)"
                     :title="`${item.account} / ${item.account_name_copy}`"
                     is-link></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="large"
                       @click="$back()"></mt-button>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';

export default {
    name: 'account-name-edit',
    data() {
        return {
            accountList: []
        }
    },
    methods: {
        getData() {
            return new Promise((resolve, reject) => {
                serviceAccount.getAccountList().then((res) => {
                    if (!res.data.status) {
                        Toast(res.data.message);
                        return;
                    }
                    resolve(res.data.list);
                }, (err) => {
                    reject(err);
                });
            });
        },
        next(item) {
            this.$router.push({ name: 'account-do-edit', params: { info: item } });
        }
    },
    created() {
        this.getData().then((data) => {
            this.accountList = data;
            this.$store.dispatch('showLoading', false);
        }, (data) => {
            Toast(data);
        });
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
.title {
    margin: 12px;
}

.page-part-top {
    border-top: 12px solid #efefef;
}
</style>


