// import './assets/scripts/flexible.js';

import Vue from 'vue';
import App from './app';

import Vuex from 'vuex';
Vue.use(Vuex);

import store from './store';

import mock from './services/mock';

import Mint from 'mint-ui-rem-dpr';
import 'mint-ui-rem-dpr/lib/style.css';
Vue.use(Mint);

import ls from './assets/scripts/vue-ls';
Vue.use(ls);

import validate from './assets/scripts/vue-validate';
Vue.use(validate);

import vueFilter from 'vue-filter';
Vue.use(vueFilter);


import VueAwesomeSwiper from 'vue-awesome-swiper';
Vue.use(VueAwesomeSwiper);

import './assets/styles/global.scss';

import router from './router/index';

import filtes from './filters/index';

import FastClick from 'fastclick';
if ('addEventListener' in document) {
    document.addEventListener('DOMContentLoaded', function() {
        FastClick.attach(document.body);
    }, false);
}

new Vue({
    router,
    store,
    el: '#app',
    render: h => h(App)
});



