<template>
    <div id="current-child-detail">
        <div>
            <mt-cell :title="` ${dataForm.card_number} `"></mt-cell>
        </div>
        <div class="page-part-top">
            <mt-cell title=""
                     :value=" dataForm.transfer_date "></mt-cell>
            <mt-cell title=""
                     :value=" dataForm.name "></mt-cell>
            <mt-cell title=""
                     :value=" dataForm.transfer_money "></mt-cell>
            <mt-cell title=""
                     :value=" dataForm.transfer_balance "></mt-cell>
            <mt-cell title=""
                     :value=" dataForm.transfer_info "></mt-cell>
        </div>
    
    </div>
</template>

<script>
export default {
    name: 'current-child-detail',
    data() {
        return {
            dataForm: {
                card_number: '',
                transfer_date: '',
                name: '',
                transfer_money: '',
                transfer_balance: '',
                transfer_info: ''
            }
        };
    },
    created() {
        this.$store.dispatch('showLoading', false);
        this.dataForm = Object.assign({}, this.dataForm, this.$route.params.info );
    }
};
</script>

<style scoped>
.page-part-top {
    border-top: 12px solid #efefef;
}
</style>


