<template>
    <div>
        <mt-cell title="" href="#/transfer/bank-card" is-link ></mt-cell>
        <mt-cell title="" href="#/transfer/bank-phone" is-link ></mt-cell>
        <mt-cell title="" href="#/transfer/bank-phone" is-link ></mt-cell>
        <mt-cell title="" href="#/transfer/bank-phone" is-link ></mt-cell>
        <mt-cell title="" href="#/transfer/bank-phone" is-link ></mt-cell>
        <mt-cell title="" class="mint-select" >
            <select name="" id="" class="mint-cell-value">
                <option value="">1</option>
                <option value="">2</option>
                <option value="">3</option>
            </select>
            <i class="mint-cell-allow-right"></i>
        </mt-cell>
        <mt-cell title="" @click.native=" popupPickerShow = true "  is-link ></mt-cell>

        <mt-popup v-model="popupPickerShow" position="bottom">
            <mt-picker :slots="slots" ></mt-picker>
        </mt-popup>

    </div>
</template>

<script>

    import popupPicker from '../../components/popup-picker';

    import { Picker } from 'mint-ui-rem-dpr';

    export default {
        data (){
            return {
                popupPickerShow: false,
                slots: [
                    {
                        flex: 1,
                        values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                        className: 'slot1',
                        textAlign: 'right'
                    }, 
                    {
                        divider: true,
                        content: '-',
                        className: 'slot2'
                    }, 
                    {
                        flex: 1,
                        values: ['2015-01', '2015-02', '2015-03', '2015-04', '2015-05', '2015-06'],
                        className: 'slot3',
                        textAlign: 'left'
                    }]
            }
        },
        components: {
            popupPicker
        },
        methods: {
            onValuesChange(picker, values) {
                if (values[0] > values[1]) {
                    picker.setSlotValue(1, values[0]);
                }
            }
        },
    }
</script>

<style lang="scss" scoped>

    @import '../../assets/styles/mixins';
    
    .mint-popup-bottom
    {
        left: 0px;
        right: 0px;
        // height: 40%;

        @include transform(translate(0px, 0px));
    }
</style>


