<template>
    <div class="account-name-editResult">
        <div class="msg"></div>
        <div class="field-action">
            <mt-button type="primary"
                       size="large"
                       @click="to"></mt-button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'account-name-editResult',
    methods: {
        to() {
            this.$router.go(-3);
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
};
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';

.account-name-editResult {
    position: relative;
    top: 120px;
    .msg {
        text-align: center;
        margin: 20px auto;
    }
}
</style>


