<template>
    <mt-header fixed :title="title">
        <a v-if=" isShowBack " @click="$back()" slot="left" class="btn-back">
            <i class="iconfont icon-hd-back"></i>
        </a>
        <a v-if=" isShowScan " class="btn-qrcode-scan" @click="scan()" slot="left">
            <i class="iconfont icon-hd-qrcode-scan"></i>
            <cite></cite>
        </a>
        <router-link to="/message-list" v-if=" isShowMessage " class="btn-message-list" slot="right">
            <i class="iconfont icon-hd-message-list"></i>
            <cite></cite>
        </router-link>
    </mt-header>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';

export default {
    computed: mapGetters({
        title: 'hdTitle',
        isShowBack: 'isShowBack',
        isShowScan: 'isShowScan',
        isShowMessage: 'isShowMessage'
    }),
    components: {
    },
    methods: {
        scan: function( data ) {
            Toast('');
        }
    }
};
</script>


<style lang="scss" scoped>
    
    @import '../assets/styles/mixins';

    .btn-back
    {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 70px;
        height: 80px;
        line-height: 80px;
        text-align: center;
        .iconfont
        {
            font-size: 28px;
        }
    }

    .btn-qrcode-scan,
    .btn-message-list
    {
        display: block;
        padding-top: 4px;
        height: 80px;
        text-align: center;
        .iconfont,cite
        {
            display: block;
            color: #fff;
        }
        .iconfont
        {
            height: 48px;
            line-height: 48px;
            font-size: 30px;
        }
        cite
        {
            font-size: 11px;

        }
    }
</style>



