<template>
    <div class="selector-bank-list">
        <mt-cell :title=" item.name " @click.native="selector( item )" is-link v-for=" (item, index) in bankList " key="index" >
            <i slot="icon" class="iconfont icon-cmbchina-logo"></i>
        </mt-cell>
    </div>
</template>

<script>

    import { Toast, IndexList, IndexSection } from 'mint-ui-rem-dpr';
    
    import * as serviceBank from '../../services/bank';
    
    export default{
        data () {
            return {
                bankList: [],
            }
        },
        created() {
            serviceBank.getBankList().then((res)=>{
                if( !res.data.status ){
                    Toast(res.data.message);    
                    return;
                }
                this.bankList = res.data.list;

                this.$store.dispatch('showLoading', false);

            }, (err)=>{
                Toast(err);
            });
        },
        methods: {
            selector ( item ){
                this.$store.dispatch('changeBank', item);
                this.$router.go( -1 );
            }
        }
    }
    
</script>

<style lang="scss">

    @import '../../assets/styles/mixins';

    .selector-bank-list
    {
        .mint-cell
        {
            .iconfont
            {
                color: #E41E26;
                vertical-align: middle;
                font-size: 48px;
            }
        }
    }
</style>


