import * as types from './mutation-types'

export const showLoading = ({ commit }, data) => 
{
    commit(types.SHOW_LOADING, data)
}

export const showHd = ({ commit }, data) => 
{
    commit(types.SHOW_HD, data)
}

export const updateHdTitle = ({ commit }, data) => 
{
    commit(types.UPDATE_HD_TITLE, data)
}

export const showBack = ({ commit }, data) => 
{
    commit(types.SHOW_BACK, data)
}

export const showTabbar = ({ commit }, data) => 
{
    commit(types.SHOW_TAB_BAR, data)
}



