<template>
    <div>
        <swiper :options="swiperOption" class="swiper">
            <swiper-slide class="swiper-item">
                <img src="../assets/images/home/img-home-swipe-item-1.jpg" alt="">
            </swiper-slide>
            <swiper-slide class="swiper-item">
                <img src="../assets/images/home/img-home-swipe-item-2.jpg" alt="">
            </swiper-slide>
            <swiper-slide class="swiper-item">
                <img src="../assets/images/home/img-home-swipe-item-3.jpg" alt="">
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>

        <ul class="icon-navs">
            <li class="item" v-for=" item in iconNavList " >
                <router-link :to=" item.path ">
                    <i class="iconfont" :class=" item.icon "></i>
                    <cite>{{ item.name }}</cite>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';

import { mapGetters, mapActions } from 'vuex';

import * as serviceHome from '../services/home';

export default {
    data() {
        return {
            swiperOption: {
                pagination: '.swiper-pagination',
                slidesPerView: 1,
                paginationClickable: true,
                spaceBetween: 30,
                loop: true
            },
            iconNavList: []
        };
    },
    created() {
        serviceHome.getHomeIconNavList().then((res)=>{
            if( !res.data.status ){
                Toast(res.data.message);    
                return;
            }
            this.iconNavList = res.data.list;

            this.$store.dispatch('showLoading', false);
            
        }, (err)=>{
            Toast(err);
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
    }
};
</script>


<style lang="scss">
    
    @import '../assets/styles/mixins';

    .swiper
    {
        .swiper-item
        {
            img
            {
                width: 10rem;
            }
        }

        .swiper-pagination
        {
            padding-right: 20px;
            bottom: 10px;
            text-align: right;
        }
        .swiper-pagination-bullet
        {
            width: 12px;
            height: 12px;
        }
        .swiper-pagination-bullet-active
        {
            background-color: #fff;
        }   

        
    }
    

</style>


<style lang="scss" scoped>
    
    @import '../assets/styles/mixins';

    .icon-navs
    {   
        @include clearfix();

        .item
        {
            float: left;
            width: 25%;
            a
            {
                display: block;
                padding: 0.4em 0px 0.4em 0px;
                text-align: center;
                min-height: 50px;

                &:active
                {
                    background-color: #f2f2f2;
                }

                .iconfont
                {
                    display: block;
                    height: 60px;
                    line-height: 60px;
                    font-size: 36px;
                    color: #93C23C;
                }

                cite
                {
                    display: block;
                    color: #333;
                    font-size: 12px;
                }
            }
        }
    }


</style>



