<template>
    <div class="account-name-doEdit">
        <mt-field label=":"
                  readonly
                  disableClear
                  v-model="editData.account"
                  type="text"></mt-field>
        <mt-field label=":"
                  readonly
                  disableClear
                  v-model="editData.account_name"
                  type="text"></mt-field>
        <mt-field label=":"
                  :placeholder="editData.account_name_copy"
                  v-model="editData.account_name_copy"
                  :attr="{ maxlength: 6 }"
                  type="text"></mt-field>
        <div class="field-action">
            <mt-button type="primary"
                       size="large"
                       @click="doEdit()"></mt-button>
        </div>
    </div>
</template>

<script>
let lsPage = ['/my-account/edit-result'];
export default {
    name: 'account-name-doEdit',
    data() {
        return {
            editData: {
                account: '',
                account_name: '',
                account_name_copy: ''
            }
        };
    },
    methods: {
        doEdit() {
            this.$router.push({ name: 'account-edit-result', params: { id: "007" } })
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
        this.editData = Object.assign({}, this.editData, this.$route.params.info);
    },
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            if (lsPage.indexOf(from.path) > -1) {
                let _temp = vm.$ls.getItem('account-name-edit');
                vm.editData = Object.assign(vm.editData, _temp);
            }
        })
    },
    beforeRouteLeave(to, from, next) {
        if (lsPage.indexOf(to.path) > -1) {
            this.$ls.setItem('account-name-edit', this.editData);
        }
        next();
    }
};
</script>


