import * as types from '../mutation-types';

const state = {
    isShowLoading: true,
    hdTitle: '',
    isShowHd: true,
    isShowBack: false,
    isShowScan: true,
    isShowMessage: true,
};

// getters
const getters = {
    isShowLoading: state => state.isShowLoading,
    hdTitle: state => state.hdTitle,
    isShowBack: state => state.isShowBack,
    isShowScan: state => state.isShowScan,
    isShowMessage: state => state.isShowMessage,
};

// actions
const actions = {
};

// mutations
const mutations = {
    [types.SHOW_LOADING] (state, data) 
    {
        state.isShowLoading = data;
    },
    
    [types.SHOW_HD] (state, data) 
    {
        state.isShowHd = data;
    },
    
    [types.SHOW_BACK] (state, data) 
    {
        state.isShowBack  = data;
        state.isShowScan = data ? false : true;
    },

    [types.UPDATE_HD_TITLE] (state, data) 
    {
        state.hdTitle  = data;
    },
};

export default {
    state,
    getters,
    actions,
    mutations
};



