<template>
    <div>
        <div class="field-part">
            <mt-cell title=""  class="mint-select" >
                <select name="" id="company" class="mint-field-core" v-model="dataForm.month">
                    <option :value="item"  v-for="item in monthList">
                        {{ item }}
                    </option>
                </select>
                <i class="mint-cell-allow-right"></i>

            </mt-cell>

        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
            <mt-button type="primary" size="large" @click.native="confrim()"></mt-button>
        </div>

    </div>
</template>

<script>

    import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
    import * as serviceLife from '../../services/life';
    let lsPage = [ '/life-pay/electricity-expense-history-detail'];
    export default {
        data (){
            return {
                dataForm: {},
                monthList: ['201704','201703','201702']
            }
        },
        created(){
            let _temp = this.$ls.getItem('ahrcu-electricity-expense');
            this.dataForm = Object.assign( this.dataForm, _temp );
        },
        methods: {
            confrim (){
                let _data = Object.assign({}, this.dataForm);
                var _error = [];
                if( this.$validate.isEmpty(  _data.month )  )
                {
                    _error.push( '' )
                }

                if( _error.length > 0 )
                {
                    Toast({
                        message: _error[0] ,
                        iconClass: 'icon-toast-error',
                        duration: 1000
                    });
                    return;
                }

                this.$router.push( 'electricity-expense-history-detail' );

            }
        },

        beforeRouteLeave (to, from, next) {
            if(lsPage.indexOf( to.path ) > -1 ){
                this.$ls.setItem('ahrcu-electricity-expense', this.dataForm );
            }
            next( true );
        },
    }

</script>


<style lang="scss" scoped>

    @import '../../assets/styles/mixins';


</style>



