import Vue from 'vue';

Date.prototype.format = function(fmt) 
{
    var o = {
        "M+": this.getMonth() + 1,
        // 
        "d+": this.getDate(),
        // 
        "H+": this.getHours(),
        // 
        "m+": this.getMinutes(),
        // 
        "s+": this.getSeconds(),
        // 
        "q+": Math.floor((this.getMonth() + 3) / 3),
        // 
        "S": this.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o) if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
};

Date.prototype.timeLen = function(type, fmt) 
{
    let o;
    if(type === "week") {
        o = {
            "y+": this.getFullYear(),
            //
            "M+": this.getMonth() + 1,
            // 
            "d+": this.getDate() - 7,
            // 
            "H+": this.getHours(),
            // 
            "m+": this.getMinutes(),
            // 
            "s+": this.getSeconds(),
            // 
            "q+": Math.floor((this.getMonth() + 3) / 3),
            // 
            "S": this.getMilliseconds() // 
        }
    }
    if(type === "month") {
        o = {
            "y+": this.getFullYear(),
            //
            "M+": this.getMonth(),
            // 
            "d+": this.getDate(),
            // 
            "H+": this.getHours(),
            // 
            "m+": this.getMinutes(),
            // 
            "s+": this.getSeconds(),
            // 
            "q+": Math.floor((this.getMonth() + 3) / 3),
            // 
            "S": this.getMilliseconds() // 
        }
    }
    if(type === "year") {
        o = {
            "y+": this.getFullYear() - 1,
            //
            "M+": this.getMonth() + 1,
            // 
            "d+": this.getDate(),
            // 
            "H+": this.getHours(),
            // 
            "m+": this.getMinutes(),
            // 
            "s+": this.getSeconds(),
            // 
            "q+": Math.floor((this.getMonth() + 3) / 3),
            // 
            "S": this.getMilliseconds() // 
        }
    }
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (o['y+'] + "").substr(4 - RegExp.$1.length));
    for (var k in o) if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
};

Vue.filter('formatDate',  (date, format)=>{

    if(!date)
    {
        return '';
    }
    var _date = null;
    if( /^\d+$/.test( date ) )
    {
        _date = new Date( date );
    }
    else
    {
        _date = new Date( date.replace(/-/g, '/') );
    }

    return _date.format( format );  
});


Vue.filter('thousands' , function( str, fixed ) {
    let thousandsRE = /\d{1,3}(?=(\d{3})+$)/g;
    let pointRe = /^(\d+)((\.\d+)?)$/;
    
    let replaceStr = '$&,';
    let numberVal = str * 1;
    let string = numberVal>=0 ? String(str) : String(str).slice(1);
    let value = string.replace(pointRe, function(str, s1, s2) {
        return s1.replace(thousandsRE, replaceStr) + s2;
    });
    return numberVal >= 0 ? value : '-' + value;
});




