import axios from 'axios';

import api from './api';

export function getHomeIconNavList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getHomeIconNavList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}



