import * as types from '../mutation-types';

const state = {
    bankName: '',
    bankCode: '',
    bankLastUpdate: '',
};

// getters
const getters = {
    bankName: state => state.bankName,
    bankCode: state => state.bankCode,
    bankLastUpdate: state => state.bankLastUpdate,
};

// actions
const actions = {
    changeBank: ({ commit }, data ) => {
        commit(types.CHANGE_BANK, data);
    },
    clearBank: ({ commit }, data ) => {
        commit(types.CLEAR_BANK, data);
    }
};

// mutations
const mutations = {
    [types.CHANGE_BANK] (state, data) 
    {
        state.bankName = data.name;
        state.bankCode = data.code;
        state.bankLastUpdate = +new Date();
    },
    
    [types.CLEAR_BANK] (state, data) 
    {
        state.bankName = '';
        state.bankCode = '';
        state.bankLastUpdate = +new Date();
    },
};

export default {
    state,
    getters,
    actions,
    mutations
};



