<template>
    <div>
        <div class="field-part">
            <mt-field label="" placeholder="" v-model=" dataForm.phone " disableClear>
                <router-link  to="#" class="btn-selector btn-seletor-payee">
                    <i class="iconfont icon-transfer-payee-name"></i>
                </router-link>
            </mt-field>
            <mt-cell title=""  class="mint-select">
                <select name="" id="" class="mint-field-core" v-model=" dataForm.amount ">
                    <option value="30">30</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                    <option value="300">300</option>
                    <option value="500">500</option>
                </select>
                <i class="mint-cell-allow-right"></i>
            </mt-cell>
            
        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
            <mt-button type="primary"  @click="next()"></mt-button>
        </div>

    </div>
</template>

<script>

    import { Toast, Actionsheet } from 'mint-ui-rem-dpr';

    let lsPage = [ '/life-pay/phone-expense-confirm'];
    
    export default {
        data (){
            return {
                iconNavList: [
                    {
                        name: '',
                        path: 'phone-expense',
                        icon: 'icon-phone-expense'
                    },
                    {
                        name: '',
                        path: 'electricity-expense',
                        icon: 'icon-electricity-expense',
                    },
                    {
                        name: '',
                        path: 'water-expense',
                        icon: 'icon-water-expenses'
                    },
                    {
                        name: '',
                        path: 'gas-expense',
                        icon: 'icon-gas-expense',
                    },
                    {
                        name: '',
                        path: 'water-expense',
                        icon: 'icon-water-expenses'
                    },
                    {
                        name: '',
                        path: 'gas-expense',
                        icon: 'icon-gas-expense',
                    },
                    {
                        name: '',
                        path: 'gas-expense',
                        icon: 'icon-gas-expense',
                    }
                ],
                dataForm: {
                    phone: '',
                    amount: 100,
                },
            }
        },
        methods: {
            next (){

                let _data = Object.assign({}, this.dataForm, this.bankCradSelector );
                var _error = [];

                if( this.$validate.isEmpty(  _data.phone )  )
                {
                    _error.push( '' )
                }
                if( !this.$validate.isPhone(  _data.phone ) )
                {
                    _error.push( '' )
                }

                if( _error.length > 0 )
                {
                    Toast({
                        message: _error[0] ,
                        iconClass: 'icon-toast-error',
                        duration: 1000
                    });
                    return;
                }

                this.$router.push( 'phone-expense-confirm' );
            }
        },
        beforeRouteEnter (to, from, next) {
            next((vm)=>{
                if(lsPage.indexOf( from.path ) > -1 ){
                    let _temp = vm.$ls.getItem('ahrcu-phone-expense');
                    vm.dataForm = Object.assign( vm.dataForm, _temp );
                }
            })
        },
        beforeRouteLeave (to, from, next) {
            if(lsPage.indexOf( to.path ) > -1 ){
                this.$ls.setItem('ahrcu-phone-expense', this.dataForm );
            }
            next( true );
        },
    }

</script>


<style lang="scss" scoped>
    
    @import '../../assets/styles/mixins';

    .icon-navs
    {
        padding: 20px 0px;
        @include clearfix();

        .item
        {
            float: left;
            width: 25%;
            a
            {
                display: block;
                padding: 0.4em 0px 0.4em 0px;
                text-align: center;
                min-height: 50px;

                &:active
                {
                    background-color: #f2f2f2;
                }

                .iconfont
                {
                    display: block;
                    height: 60px;
                    line-height: 60px;
                    font-size: 36px;
                    color: #93C23C;
                }

                cite
                {
                    display: block;
                    color: #333;
                    font-size: 12px;
                }
            }
        }
    }

</style>


