<template>
    <div class="wp" :class="{ 'hide-hd': !isShowHd, 'hide-tabbar': !isShowTabbar }"  >
        <hd v-if=" isShowHd "></hd>
   	    <tabbar v-if=" isShowTabbar "></tabbar>
        <div class="page-wp">
            <router-view></router-view>
        </div>

        <div class="page-loading" v-if=" isShowLoading " >
            <div class="indicator-spin"></div>
        </div>
    </div>
</template>

<script>

import hd from './components/hd';
import tabbar from './components/tabbar';

import { mapGetters, mapActions } from 'vuex';


export default {
    name: 'app',
    mounted (){
        this.$store.dispatch('showLoading', false);
    },
    computed: mapGetters({
        isShowHd: 'isShowHd',
        isShowTabbar: 'isShowTabbar',
        isShowLoading: 'isShowLoading'
    }),
    components: {
        hd, tabbar
    }
};
</script>



