<template>
    <div>
        <div class="field-part">
            <mt-cell title="" :value=" bankCradSelector.payeeName " ></mt-cell>
            <mt-cell title="" :value=" bankCradSelector.payeeAccount " ></mt-cell>
            <mt-cell title="" :value=" bankCradSelector.payeeBankName " ></mt-cell>
            <mt-cell title="" :value=" dataForm.tranAccount " ></mt-cell>
            <mt-cell title="" :value=" dataForm.tranAmount " ></mt-cell>
        </div>

        <div class="field-part">
            <mt-cell title="" :value=" dataForm.phone " ></mt-cell>
            <mt-cell title="" :value=" dataForm.remark " ></mt-cell>
        </div>
        
        <div class="field-part">
            <mt-field label="" placeholder="" type="password" v-model=" dataForm.payPwd "></mt-field>
            <mt-field label="" placeholder="" type="number" max-length="6" v-model=" dataForm.payCode "></mt-field>
        </div>

        <div class="field-action">
            <mt-button type="default"  @click="$back()"></mt-button>
            <mt-button type="primary" size="large" @click.native="submit()"></mt-button>
        </div>
    </div>
</template>
<script>

    import { Toast, Indicator } from 'mint-ui-rem-dpr';

    import { mapGetters, mapActions } from 'vuex';

    export default {
        data (){
            return {
                dataForm: {},
            }
        },
        created(){
            let _temp = this.$ls.getItem('ahrcu-bank-card');
            this.dataForm = Object.assign( this.dataForm, _temp );
        },
        computed: {
            ...mapGetters(['bankCradSelector'])
        },
        methods: {
            submit (){
                let _data = Object.assign({}, this.dataForm, this.bankCradSelector );
                var _error = [];

                if( this.$validate.isEmpty(  _data.payPwd ) )
                {
                    _error.push( '' )
                }
                if( this.$validate.isEmpty(  _data.payCode ) )
                {
                    _error.push( '' )
                }

                if( _error.length > 0 )
                {
                    Toast({
                        message: _error[0] ,
                        iconClass: 'icon-toast-error',
                        duration: 1000
                    });
                    return;
                }

                Indicator.open({
                    text: '...',
                    spinnerType: 'fading-circle'
                });

                setTimeout(()=>{
                    Indicator.close();
                    this.$router.push( 'success' );
                }, 3000);

            }
        }
    }
</script>

<style lang="scss" scoped>
    
    @import '../../assets/styles/mixins';


</style>


