<template>
    <div id="current-child">
        <mt-navbar class="page-part"
                   v-model="selected">
            <mt-tab-item id="all"></mt-tab-item>
            <mt-tab-item id="in"></mt-tab-item>
            <mt-tab-item id="out"></mt-tab-item>
        </mt-navbar>
    
        <mt-tab-container class="page-part-top"
                          v-model="selected">
            <mt-tab-container-item id="all">
                <div class="page-loadmore-wrapper"
                     ref="wrapper"
                     :style="{ height: wrapperHeight + 'px' }">
                    <mt-loadmore :bottom-method="loadBottom"
                                 @bottom-status-change="handleBottomChange"
                                 :bottom-all-loaded="allLoaded"
                                 ref="loadmore">
                        <ul class="cell-list">
                            <router-link :to="{ name: 'account-transfer-detail', params: { info: item }}"
                                         tag="li"
                                         append
                                         v-for="item in transferList"
                                         :key="item.serial_number">
                                <div class="cell-left">
                                    <div class="cell-name">{{item.serial_number}}</div>
                                    <div class="cell-balance">:{{item.transfer_balance}}</div>
                                </div>
                                <div class="cell-right">
                                    <div class="cell-money">{{item.transfer_money}}</div>
                                    <div class="cell-date">{{item.transfer_date}}</div>
                                </div>
                                <div class="cell-arrow">
                                    >
                                </div>
                            </router-link>
                        </ul>
                        <div slot="bottom"
                             class="mint-loadmore-bottom">
                            <span v-show="bottomStatus !== 'loading'"
                                  :class="{ 'is-rotate': bottomStatus === 'drop' }"></span>
                            <span v-show="bottomStatus === 'loading'">
                            <mt-spinner type="snake"></mt-spinner>
                          </span>
                        </div>
                    </mt-loadmore>
                </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="in">
                <ul class="cell-list">
                    <router-link :to="{ name: 'account-transfer-detail', params: { info: item }}"
                                 tag="li"
                                 append
                                 v-for="item in transferList"
                                 :key="item.serial_number">
                        <div class="cell-left">
                            <div class="cell-name">{{item.name}}</div>
                            <div class="cell-balance">:{{item.transfer_balance}}</div>
                        </div>
                        <div class="cell-right">
                            <div class="cell-money">{{item.transfer_money}}</div>
                            <div class="cell-date">{{item.transfer_date}}</div>
                        </div>
                        <div class="cell-arrow">
                            >
                        </div>
                    </router-link>
                </ul>
            </mt-tab-container-item>
            <mt-tab-container-item id="out">
                <ul class="cell-list">
                    <router-link :to="{ name: 'account-transfer-detail', params: { info: item }}"
                                 tag="li"
                                 append
                                 v-for="item in transferList"
                                 :key="item.serial_number">
                        <div class="cell-left">
                            <div class="cell-name">{{item.name}}</div>
                            <div class="cell-balance">:{{item.transfer_balance}}</div>
                        </div>
                        <div class="cell-right">
                            <div class="cell-money">{{item.transfer_money}}</div>
                            <div class="cell-date">{{item.transfer_date}}</div>
                        </div>
                        <div class="cell-arrow">
                            >
                        </div>
                    </router-link>
                </ul>
            </mt-tab-container-item>
        </mt-tab-container>
    </div>
</template>

<script>
import { Toast } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/transfer-list';
let lsPage = ['/my-account/current-list/detail'];
export default {
    name: 'current-child',
    data() {
        return {
            selected: 'all',
            transferList: [],
            allLoaded: false,
            bottomStatus: '',
            wrapperHeight: 0
        };
    },
    methods: {
        handleBottomChange(status) {
            this.bottomStatus = status;
        },
        loadBottom() {
            let lastValue = this.transferList.length - 1;
            if (lastValue < 21) {
                for (let i = 1; i <= 10; i++) {
                    this.transferList.push(lastValue + i);
                }
            } else {
                this.allLoaded = true;
            }
            this.$refs.loadmore.onBottomLoaded();
        },
        selectedData(id, data) {
            if (id === "all") {
                this.transferList = data;
            }
            else {
                let _copy_ = data.filter((item) => {
                    return item.status == id;
                });
                this.transferList = _copy_;
            }
        },
        getData() {
            return new Promise((resolve, reject) => {
                serviceAccount.getTansferList().then((res) => {
                    if (!res.data.status) {
                        Toast(res.data.message);
                        return;
                    }
                    resolve(res.data.list);
                }, (err) => {
                    reject(err);
                });
            });
        },
        selectedChange(val, oldVal) {
            this.getData().then((data) => {
                this.selectedData(val, data);
            })
        }
    },
    watch: {
        selected: 'selectedChange'
    },
    created() {
        this.getData().then((data) => {
            this.selectedData(this.selected, data);
            this.$store.dispatch('showLoading', false);
        }, (data) => {
            Toast(data);
        });
    },
    mounted() {
        this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top + 120;
    },
    beforeRouteEnter(to, from, next) {
        next((vm) => {
            if (lsPage.indexOf(from.path) > -1) {
                let _temp = vm.$ls.getItem('transfer-detail-selected');
                vm.selected = _temp;
            }
        })
    },
    beforeRouteLeave(to, from, next) {
        if (lsPage.indexOf(to.path) > -1) {
            this.$ls.setItem('transfer-detail-selected', this.selected);
        }
        next();
    }
};
</script>

<style lang="scss" scoped>
@import '../../assets/styles/mixins';
.page-part {
    padding-bottom: 3px;
}

.page-part-top {
    border-top: 12px solid #efefef;
}

.cell-list {
    background-color: #fff;
    box-sizing: border-box;
    color: inherit;
    min-height: 48px;
    display: block;
    overflow: hidden;
    position: relative;
    text-decoration: none;
    li {
        background-image: -webkit-linear-gradient(top, #d9d9d9, #d9d9d9 50%, transparent 0);
        background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 0);
        background-size: 120% 1px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-origin: content-box;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        box-sizing: border-box;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        font-size: 16px;
        line-height: 1;
        min-height: inherit;
        overflow: hidden;
        padding: 0 10px;
        width: 100%;
    }
}

.cell-list li:last-child {
    border-bottom: 1px solid #d9d9d9;
}

.cell-left {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}

.cell-name {
    margin: 6px 0px;
}

.cell-balance {
    color: #888;
    margin: 6px 0px;
}

.cell-money {
    margin: 6px 0px;
}

.cell-date {
    color: #888;
    margin: 6px 0px;
}

.cell-right {
    text-align: right;
}

.cell-arrow {
    margin: 0px 6px;
}
</style>


