export const isShowHd = state => {
    return state.hd.isShowHd;
};

export const isShowTabbar = state => {
    return state.tabbar.isShowTabbar;
};

export const tabnavId = state => {
    return state.tabbar.tabnavId;
};

export const tabnavList = state => {
    return state.tabbar.tabnavList;
};


export const bankCradSelector = state => {

    let _selectorPayee = state.selectorPayee;
    let _selectorBank = state.selectorBank;
    let _temp = Object.assign({}, _selectorPayee, _selectorBank);

    _temp.payeeBankName  = ( _selectorPayee.payeeLastUpdate > _selectorBank.bankLastUpdate ) ? _selectorPayee.payeeBankName : _selectorBank.bankName;
    
    return _temp;
};







